import Link from 'next/link';
import fetch from 'isomorphic-unfetch';

const Index = props => (
    <div>
<meta httpEquiv="Access-Control-Allow-Origin" content="*"></meta>
        <Link href="/about">
            <a>About Page</a>
        </Link>
        <p>{props.data}</p>
    </div>
);

Index.getInitialProps = async function () {
    const res = await fetch('https://dy-api.com/v2/serve/user/choose', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            "DY-API-Key": "0627850b8c9e4ff048bb8bbaee9527c2d525c79c754befcd6cbd71284050896c"
        },
        body: JSON.stringify({
            "user": { "id": "saul" },
            "sessionId": "6eab6b44a4cbd563376663e8b5f24e92",
            "selector": { "names": ["recs"] },
            "context": {
                "page": {
                    "type": "OTHER",
                    "data": [],
                    "location": "www.example.com",
                    "locale": "en_US"
                }
            }
        })
    }).then(r => {
        return r;
    })


    const data = await res.json();

    return {
        data
    };
};

export default Index;